.calendar {
  grid-template-columns: repeat(7, 1fr);
  gap: .25rem;
  margin-top: .5rem;
  display: grid;
}

.calendar-header {
  background-color: #f3f4f6;
  border: 1px solid #e5e7eb;
  padding: .4rem;
  font-size: .875rem;
  font-weight: 600;
}

.calendar-day {
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #e5e7eb;
  padding: .4rem;
  font-size: .875rem;
}

.calendar-day:hover {
  background-color: #e5e7eb;
}

.calendar-day.selected:hover {
  background-color: #f87171;
}

.selected {
  color: #fff;
  background-color: #f87171;
}

#json-output {
  white-space: pre-wrap;
  word-wrap: break-word;
}

#calendar-wrapper {
  height: auto;
  padding-bottom: .5rem;
  overflow-y: visible;
}
